// В этом файле должны быть стили для БЭМ-блока copyright, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.copyright {
  display: flex;
  flex-wrap: wrap;
  align-items: center;

  font-size: calc(var(--text-size) - 2px);
  color: lighten($main-text-color, 20);
}

.copyright__link {
  position: relative;

  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 72px;
  height: 68px;
  margin-right: 40px;

  color: lighten($main-text-color, 20);

  background-color: lighten(black, 10);
  border: 1px solid rgba($alter-bg-color, 0.1);
  will-change: transform;
  transition: transform 0.2s ease-in;

  &:hover,
  &:focus {
    border-color: rgba($alter-bg-color, 0.3);
    transform: translateY(-5px);
    will-change: transform;
    transition: transform 0.2s ease-out;
  }

  &:active {
    opacity: 0.5;
  }
}

.copyright__img {
  display: block;
  width: 27px;
  height: 34px;
}

@media (max-width: $desktop-width - 1px) {
  .copyright {
    font-size: var(--text-size);
  }

  .copyright__link {
    margin-right: 20px;
  }
}
